/* ===== GLOBAL RESET ===== */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  line-height: 1.6;
}/* ===== Banner Section ===== */
.banner-section {
  width: calc(100% - 40px);
  height: 300px;
  background-image: url("https://help.whatnot.com/hc/theming_assets/01JV8AH6DVE2S4T1FTQ47FVFZB");
  background-size: cover;
  background-position: center;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  color: #fff;

  /*  Proper spacing */
  margin: 12px 20px 20px 20px; /* top, right, bottom, left */
  padding: 30px 40px;
  border-radius: 15px;
  box-sizing: border-box;

  /*  Do NOT hide margins */
  overflow: visible; 
}

.banner-section {
  margin-top: 105px !important;
}

/* ===== Overlay ===== */
.banner-section::before {
  content: "";
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.4);
  z-index: 0;
  border-radius: 15px;
}

/* Ensure content stays above overlay */
.banner-section * {
  position: relative;
  z-index: 1;
}

/* ===== Breadcrumb ===== */
.breadcrumb {
  font-size: 16px;
  margin-bottom: 20px;
  text-align: left;
}

.breadcrumb a {
  color: #fff;
  text-decoration: none;
}

.breadcrumb a:hover {
  color: #ffd700; /* gold/yellow highlight */
   
}

.breadcrumb span {
  color: #ddd;
}

.breadcrumb span {
  color: #ddd;
}

/* ===== Heading ===== */
h1 {
  font-size: 36px;
  margin: 0;
  padding-bottom: 10px;
  text-align: left;
}

/* ===== Paragraph ===== */
p {
  font-size: 18px;
  margin: 0;
  padding-top: 10px;
  text-align: left;
  max-width: 600px;
  line-height: 1.6;
}

/* ===== Responsive ===== */
@media (max-width: 768px) {
  .banner-section {
    width: calc(100% - 20px);
    height: 250px;
    margin:120px 10px 10px 10px; /*  keep top space on mobile too */
    padding: 15px 20px;
  }

  .breadcrumb {
    font-size: 14px;
    margin-bottom: 10px;
  }

  h1 {
    font-size: 24px;
    padding-bottom: 6px;
  }

  p {
    font-size: 14px;
    padding-top: 6px;
  }
}


/* ===== Report Form Container ===== */
.report-form-section {
  width: 85%;
  max-width: 800px;
  margin: 50px auto;
  padding: 40px;
  background: linear-gradient(180deg, #fffde7 0%, #fff9c4 100%);
  border: 2px solid #fbc02d; /*  Yellow border color */
  border-radius: 20px;
  box-shadow: 0 6px 20px rgba(251, 192, 45, 0.15);
  font-family: "Poppins", sans-serif;
  line-height: 1.7;
  text-align: left;
}

/* ===== Title & Description ===== */
.report-form-title {
  text-align: center;
  font-size: 24px;
  font-weight: 700;
  color: #333;
  margin-bottom: 10px;
}

.report-form-desc {
  text-align: center;
  color: #666;
  margin-bottom: 30px;
}

/* ===== Form Labels ===== */
.report-form-label {
  font-weight: 600;
  color: #333;
  display: block;
  margin-bottom: 8px;
}

/* ===== Input Fields ===== */
.report-form-input,
.report-form-textarea {
  width: 100%;
  padding: 12px;
  border: 2px solid #ddd;
  border-radius: 10px;
  margin-bottom: 20px;
  font-size: 15px;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.report-form-input:focus,
.report-form-textarea:focus {
  border-color: #fbc02d;
  box-shadow: 0 0 6px rgba(251, 192, 45, 0.4);
  outline: none;
}

/* ===== Button ===== */
.report-form-button {
  background-color: #fbc02d;
  color: #111;
  border: none;
  border-radius: 10px;
  padding: 12px 25px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: block;
  margin: 0 auto;
}

.report-form-button:hover {
  background-color: #fdd835;
}

/* ===== Required Star ===== */
.required-star {
  color: red;
  margin-left: 3px;
}
